import React, { Component } from 'react';
import {
  View,
  Button,
  TextInput,
  Text,
  ActivityIndicator
} from 'react-native';

import Header from '../Header';
import Rows from '../Rows';

const axios = require('axios');
const moment = require('moment');
const lib = require('../lib');
const HOST = lib.HOST;

export default class Screen extends Component {
  constructor() {
    super();
    this.state = {
      show:"list",
      rows1:[],
      rows2:[],
    }
  }

  componentDidMount() {
    this._tick = setInterval(()=>this.refresh(),1000);
  }

  componentWillUnmount() {
    if(this._tick) clearInterval(this._tick);
    if(this._busyRefresh) this._busyRefresh = null;
  }

  refresh() {
    if(this._busyRefresh) return;
    this._busyRefresh = true;

    this.refreshList()
    .then(()=>{
      return this.refreshList2();
    })
    .then(()=>{
      this._busyRefresh = false;
    });
  }

  refreshList() {
    return this.getKDE()
      .then(kdes=>{
        if(!kdes) return;

        const rows = [];

        for(let i=0;i<kdes.length;i++) {
          const obj = kdes[i];
          const code = obj.code;
          const species = obj.species;
          const farm = obj.farm;
          const byCatch = obj.byCatch;
          // const verifiedBy = (obj.verifiedBy).split('#')[1];

          const row1 = code;
          let row2 = "species:"+species;
          const row3 = "farm:"+farm+" byCatch:"+byCatch;
          rows.push({
            id:obj.code,
            row1:row1,
            row2:row2,
            row3:row3,
            obj:obj
          });
        }


        this.setState({rows1:rows});
      });
  }

  refreshList2() {
    return this.getVerifiedKDE()
      .then(kdes=>{
        if(!kdes) return;

        const rows = [];

        for(let i=0;i<kdes.length;i++) {
          const obj = kdes[i];
          const code = obj.code;
          const species = obj.species;
          const farm = obj.farm;
          const byCatch = obj.byCatch;
          const verifiedBy = (obj.verifiedBy).split('#')[1];

          const row1 = code;
          const row2 = "species:"+species+" farm:"+farm+" byCatch:"+byCatch;
          const row3 = "verified by:" + verifiedBy;
          rows.push({
            id:obj.code,
            row1:row1,
            row2:row2,
            row3:row3,
            obj:obj
          });
        }


        this.setState({rows2:rows});
      });
  }

  onClickVerify(obj) {
    console.log(obj);
    this.setState({show:'busy'});
    const code = obj.code;
    this.postVerifyKDE(code)
    .then(ok=>{
      if(!ok) {        
        lib.alert('Transaction failed!');
        this.setState({show:'list'});
      } else {
        lib.alert('Transaction success!');
        this.setState({show:'list'});
      }
    });
  }

  render() {

    let list;
    if(this.state.show === "busy") 
      list = <View style={{flex:1,alignItems:'center',justifyContent:'center'}}><ActivityIndicator /></View>;
    else {
      const rows = this.state.rows1;

      list = (
        <View style={{flex:1}}>
          <Rows rows={rows} onClickQrCode={obj=>this.onClickVerify(obj)} mode="neptune" />
        </View>
      );
    }

    const rows2 = this.state.rows2;
    const list2 = (
      <View style={{flex:1}}>
        <Rows rows={rows2} onClickQrCode={obj=>console.log(obj)} mode="non-neptune" />
      </View>
    );

    return (
      <View style={{flex:1}}>
        <Header title="VERIFIKATOR MENU" email="neptune@sea.com" history={this.props.history} />
        <View style={{flex:1,flexDirection:'row'}}>          
          <View style={{flex:1,padding:10}}>
            <Text style={{fontWeight:'bold',textAlign:'center'}}>Unverified KDE(s)</Text>
            {list}
          </View>  
          <View style={{flex:1,padding:10}}>
            <Text style={{fontWeight:'bold',textAlign:'center'}}>Verified KDE(s)</Text>
            {list2}
          </View>  
        </View>        
      </View>
    );
  }

  getKDE() {
    const url = HOST+'/api/KDE';
    console.log(url);
    return new Promise((resolve,reject)=>{
      axios.get(url)
      .then(response => {
        resolve(response.data);
      })
      .catch(error => {
        console.error(error);
        resolve(null);
      })  
    });
  }

  getVerifiedKDE() {
    const url = HOST+'/api/VerifiedKDE';
    return new Promise((resolve,reject)=>{
      axios.get(url)
      .then(response => {
        resolve(response.data);
      })
      .catch(error => {
        console.error(error);
        resolve(null);
      })  
    });
  }

  postVerifyKDE(code) {
    const url = HOST+'/api/VerifyKDE';
    return new Promise((resolve,reject)=>{
      axios.post(url,{
        "$class": "com.altermyth.mynetwork.VerifyKDE",
        "kde": "resource:com.altermyth.mynetwork.KDE#"+code,
        "verificator": "resource:com.altermyth.mynetwork.Verificator#neptune@sea.com"
      })
      .then(response => {
        resolve(true);
      })
      .catch(error => {
        console.log(error);
        resolve(false);
      })  
    });
  }
}